import React from 'react';
import {Route, Switch, useLocation, Redirect, useHistory} from "react-router-dom";
import NewCampaign from "./Campaign/NewCampaign";
import NewContact from "./NewContact";
import Contacts from "./Contacts";
import SignIn from "./SignIn";

export default function RedirectHandler() {
    const history = useHistory();
    const redirectRoute = new URLSearchParams(useLocation().search).get('redirect');
    if (redirectRoute) {
        const actualRoute = redirectRoute.replace(/\/\//g, '/');
        history.push(actualRoute);
        return (<div></div>)
    } else {
        return (
            <Switch>
                <Route path="/campaigns/new">
                    <NewCampaign/>
                </Route>
                <Route path="/contacts/new">
                    <NewContact/>
                </Route>
                <Route path="/contacts">
                    <Contacts/>
                </Route>
                <Route path="/signIn">
                    <SignIn/>
                </Route>
                <Route path="*">
                    <Redirect to={"/contacts"} />
                </Route>
            </Switch>
        );
    }
}

